import { BehaviorSubject } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from '@environments/environment';
import { map } from 'rxjs/operators';
import {
  ResponseStyles,
  ResponsePriceRanges,
  Franchise,
  ResponseFranchises,
  ResponseManagers
} from '@models';
import { IFranchise } from '@interfaces';
@Injectable({
  providedIn: 'root'
})
export class FranchiseService {
  public franchise = new BehaviorSubject<Franchise>(null);
  public currentFranchise = this.franchise.asObservable();

  constructor(public http: HttpClient) {}

  public setFranchise(franchise: Franchise): void {
    this.franchise.next(franchise);
  }

  public createFranchise(franchise: Franchise): Observable<Franchise> {
    return this.http.post<Franchise>(environment.FRANCHISE, franchise);
  }

  public getStyles(): Observable<ResponseStyles> {
    return this.http.get<ResponseStyles>(environment.STYLES);
  }

  public getFranchiseDetails(id: number) {
    return this.http.get(`${environment.FRANCHISE}/${id}/detail`);
  }

  public getFranchiseManagers(
    franchiseId: number
  ): Observable<ResponseManagers> {
    const url = environment.FRANCHISE_MANAGERS.replace(
      '{franchiseId}',
      franchiseId.toString()
    );
    return this.http.get<ResponseManagers>(url);
  }

  public updateFranchise(
    id: number,
    payload: Franchise
  ): Observable<IFranchise> {
    return this.http.put<IFranchise>(`${environment.FRANCHISE}/${id}`, payload);
  }

  public getPriceRanges(): Observable<ResponsePriceRanges> {
    return this.http.get<ResponsePriceRanges>(environment.PRICE_RANGES);
  }

  public getFranchises(): Observable<ResponseFranchises> {
    return this.http
      .get<ResponseFranchises>(environment.FRANCHISES)
      .pipe(map((response) => response as ResponseFranchises));
  }

  public getFranchisesAsPromise(): Promise<ResponseFranchises> {
    return this.http
      .get<ResponseFranchises>(environment.FRANCHISES)
      .toPromise();
  }
}
