import { Component, OnDestroy } from '@angular/core';
import { FormControl, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import { MatSnackBar } from '@angular/material/snack-bar';
import * as firebase from 'firebase/app';
import { LoginErrorCodePipe } from '@pipes/login-error-code/login-error-code.pipe';
import {
  TopLoaderService,
  FranchiseService,
  RestaurantService,
} from '@services';
import { ResponseFranchises, ResponseRestaurant } from '@models';
import { Subscription } from 'rxjs';
import { version } from '../../../../package.json';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
  preserveWhitespaces: true,
  providers: [LoginErrorCodePipe],
})
export class LoginComponent implements OnDestroy {
  public version: string = version;

  /**
   * Form used to manage the login fields.
   */
  public loginForm = this.fb.group({
    email: new FormControl('', []),
    password: new FormControl('', []),
  });

  /**
   * Variable used to store the subscription that give us the current franchise data.
   * We save that subscription so that later we can unsubscribe it.
   */
  private franchisesSubscription = Subscription.EMPTY;

  /**
   * Variable used to store the subscription that give us the current restaurant data.
   * We save that subscription so that later we can unsubscribe it.
   */
  private restaurantsSubscription = Subscription.EMPTY;

  constructor(
    private fb: FormBuilder,
    private router: Router,
    private dialog: MatSnackBar,
    private loginErrorPipe: LoginErrorCodePipe,
    private topLoaderService: TopLoaderService,
    private franchiseService: FranchiseService,
    private restaurantService: RestaurantService
  ) {}

  ngOnDestroy() {
    this.franchisesSubscription.unsubscribe();
    this.restaurantsSubscription.unsubscribe();
  }

  /**
   * Handle the user's intent to login.
   */
  public login(): void {
    this.topLoaderService.showLoader();
    const email = this.loginForm.get('email').value;
    const password = this.loginForm.get('password').value;
    firebase
      .auth()
      .signInWithEmailAndPassword(email, password)
      .then(() => {
        firebase
          .auth()
          .currentUser.getIdTokenResult()
          .then((idTokenResult) => {
            const hasUserAdminPrivileges = idTokenResult.claims.admin;
            const hasUserStaffPrivileges = idTokenResult.claims.staff;

            // Check if the user has admin or staff privileges.
            if (hasUserAdminPrivileges || hasUserStaffPrivileges) {
              this.franchisesSubscription = this.franchiseService
                .getFranchises()
                .subscribe((franchisesResponse: ResponseFranchises) => {
                  this.franchisesSubscription.unsubscribe();

                  this.topLoaderService.hiddeLoader();

                  // If there is no franchise created the user is sent to the onboarding.
                  if (franchisesResponse.franchises.length === 0) {
                    this.router.navigate(['/onboarding']);
                  } else {
                    // If there is at least one franchise created we save the id of the first one on localStorage
                    // so it will be considered as the selected one.
                    localStorage.setItem(
                      'franchiseId',
                      JSON.stringify(franchisesResponse.franchises[0].id)
                    );

                    this.restaurantsSubscription = this.restaurantService
                      .getRestaurants(franchisesResponse.franchises[0].id)
                      .subscribe((responseRestaurant: ResponseRestaurant) => {
                        this.restaurantsSubscription.unsubscribe();

                        // If there is no restaurant created on the franchise we send the user to the onboarding
                        if (
                          franchisesResponse.franchises[0].restaurants
                            .length === 0
                        ) {
                          if (hasUserAdminPrivileges) {
                            this.router.navigate(['/onboarding']);
                          } else {
                            // If the user isn't an admin and has no restaurants assigned we send an error.
                            firebase.auth().signOut();
                            localStorage.clear();
                            this.showError(
                              'El personal no está asignado en alguna sucursal'
                            );
                            this.topLoaderService.hiddeLoader();
                          }
                        } else {
                          // If there is at least one restaurant created we save the id of the first one on localStorage
                          // so it will be considered as the selected one.
                          localStorage.setItem(
                            'restaurantId',
                            JSON.stringify(
                              franchisesResponse.franchises[0].restaurants[0].id
                            )
                          );

                          // Depending on the user's privileges we send it to the specific layout
                          if (hasUserAdminPrivileges) {
                            this.router.navigate(['/admin']);
                          } else if (hasUserStaffPrivileges) {
                            this.router.navigate(['/staff']);
                          } else {
                            // If the user isn't admin neither staff we finish its session.
                            firebase.auth().signOut();
                            this.topLoaderService.hiddeLoader();
                            this.showError('auth/not-an-admin');
                          }
                        }
                      });
                  }
                });
            } else {
              // If the user doesn't have privileges the firebase session is signed out and an error alert is showed.
              firebase.auth().signOut();
              this.topLoaderService.hiddeLoader();
              this.showError('auth/not-an-admin');
            }
          })
          .catch((error) => {
            this.showError(error.code);
          });
      })
      .catch((error: any) => {
        this.topLoaderService.hiddeLoader();
        this.showError(error.code);
      });
  }

  /**
   * Shows a snackbar with an error message.
   * @param errorCode Message to show at the snackbar.
   */
  private showError(errorCode: string): void {
    this.dialog.open(this.loginErrorPipe.transform(errorCode), 'Entendido', {
      duration: 15000,
    });
  }
}
