import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '@environments/environment';
import { Observable } from 'rxjs';
import {
  Amenities,
  AmenitiesToSave,
  Extra,
  ExtrasToSave,
  ResponseDetail,
  ResponseExtras,
  Restaurant,
  WorkingHoursResponse
} from '@models';
import { ResponseRestaurant } from '@models';
import { BehaviorSubject } from 'rxjs';
import { ResponseUsers } from '@models';

@Injectable({
  providedIn: 'root'
})
export class RestaurantService {
  public restaurant = new BehaviorSubject<Restaurant>(null);
  public currentRestaurant = this.restaurant.asObservable();

  constructor(
    public http: HttpClient,
    ) {}

  setRestaurant(restaurant: Restaurant): void {
    this.restaurant.next(restaurant);
  }

  public getRestaurants(franchiseId: number): Observable<ResponseRestaurant> {
    const url = environment.FRANCHISE_RESTAURANTS.replace(
      '{franchiseId}',
      franchiseId.toString()
    );
    return this.http.get<ResponseRestaurant>(url);
  }

  public getRestaurantsAsPromise(
    franchiseId: number
  ): Promise<ResponseRestaurant> {
    const url = environment.FRANCHISE_RESTAURANTS.replace(
      '{franchiseId}',
      franchiseId.toString()
    );
    return this.http.get<ResponseRestaurant>(url).toPromise();
  }

  getRestaurantDetail(restaurantId: number): Observable<ResponseDetail> {
    const url = environment.RESTAURANT_DETAIL.replace(
      '{restaurantId}',
      restaurantId.toString()
    );
    return this.http.get<ResponseDetail>(url);
  }

  updateRestaurantDetails(restaurantId: string, payload: any) {
    const url = environment.RESTAURANT.replace('{restaurantId}', restaurantId);
    return this.http.put(`${url}`, payload);
  }

  public getRestaurantUsers(restaurantId: number): Observable<ResponseUsers> {
    const url = environment.RESTAURANT_USERS.replace(
      '{restaurantId}',
      restaurantId.toString()
    );
    return this.http.get<ResponseUsers>(url);
  }

  public createRestaurant(
    restaurant: Restaurant,
    franchiseId: number
  ): Observable<Restaurant> {
    const url = environment.FRANCHISE_RESTAURANT.replace(
      '{franchiseId}',
      franchiseId.toString()
    );
    return this.http.post<Restaurant>(url, restaurant);
  }

  public updateRestaurant(
    restaurant: Restaurant,
    restaurantId: number
  ): Observable<Restaurant> {
    const url = environment.RESTAURANT.replace(
      '{restaurantId}',
      restaurantId.toString()
    );
    return this.http.put<Restaurant>(url, restaurant);
  }

  public deleteRestaurant(restaurantId: number): Observable<Restaurant> {
    const url = environment.RESTAURANT.replace(
      '{restaurantId}',
      restaurantId.toString()
    );
    return this.http.delete<Restaurant>(url);
  }

  public createExtras(
    extras: ExtrasToSave,
    restaurantId: number
  ): Observable<ResponseExtras> {
    const url = environment.RESTAURANT_EXTRAS.replace(
      '{restaurantId}',
      restaurantId.toString()
    );
    return this.http.post<ResponseExtras>(url, extras);
  }

  deleteExtraAmenity(restaurantId: number, extraId: number): Observable<any> {
    const url = environment.RESTAURANT_EXTRA.replace(
      '{restaurantId}',
      restaurantId.toString()
    );
    return this.http.delete<void>(`${url}/${extraId}`);
  }

  public setRestaurantAmenities(
    amenities: AmenitiesToSave,
    restaurantId: number
  ): Observable<void> {
    const url = environment.RESTAURANT_AMENITIES.replace(
      '{restaurantId}',
      restaurantId.toString()
    );
    return this.http.put<void>(url, amenities);
  }

  public setRestaurantGallery(
    gallery: FormData,
    restaurantId: number
  ): Observable<void> {
    const url = environment.RESTAURANT_MEDIA.replace(
      '{restaurantId}',
      restaurantId.toString()
    );
    return this.http.put<void>(url, gallery);
  }

  public removeRestaurantGalleryImage(
    restaurantId: number,
    imageId: number
  ): Observable<any> {
    const url = environment.RESTAURANT_IMAGE.replace(
      '{restaurantId}',
      restaurantId.toString()
    ).replace('{imageId}', imageId.toString());
    return this.http.delete<void>(url);
  }

  public setRestaurantSchedules(
    schedules: WorkingHoursResponse,
    restaurantId: number
  ): Observable<void> {
    const url = environment.RESTAURANT_WORKING_HOURS.replace(
      '{restaurantId}',
      restaurantId.toString()
    );
    return this.http.put<void>(url, schedules);
  }

  public getAmenities(): Observable<Amenities> {
    return this.http.get<Amenities>(environment.AMENITIES);
  }

  public createExtra(extra: Extra, restaurantId: number): Observable<Extra> {
    const url = environment.RESTAURANT_EXTRA.replace(
      '{restaurantId}',
      restaurantId.toString()
    );
    return this.http.post<Extra>(url, extra);
  }
}
